@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,500;0,700;0,900;1,300&display=swap');

*{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;

}

*::-webkit-scrollbar {
    display: none;
}

a,h2{
    color: white;
}

a{
    text-decoration: none;
}

$bg: rgb(0, 0, 0);


.header{
    height: 100px;
    background-color: $bg;
    display: flex;
    align-items: center;
    padding: 2rem;
    box-sizing: border-box;
    justify-content: space-between;

    > img{
        height: 70%;
    }
    
    >div{
        width: 100%;
        margin-left: 4vmax ;

        >a{
            margin: 2vmax;
            font-size: 1.3rem;
            font-weight: 300;
        }

    }

    >svg{
        color: white;
        font-size: 2rem;
        cursor: pointer;

        &:hover{
            color: rgb(167, 167, 167);
        }
    }
}
